.list-items {
}

.list-group-item:not(:last-child) {
    border-bottom: solid 1px #bed1e6;
}

.list-item {
    cursor: pointer;
}

.list-item:hover {
    background-color: #bed1e6;
}

.item-card {
    margin: 30px 0 0 0;
}

.img-card-container {
    padding: 15px;
    width: 250px;
    height: 250px;
}

.img-card {
    border-radius: 6px;
    width: 100%;
    height: 100%;
}